$primary: #262014;

.progress-bar {
  &.color-nodejs {

    &,
    &:after {
      background-color: #73b958;
    }

    span {
      color: #73b958;
    }
  }

  &.color-angular {

    &,
    &:after {
      background-color: #dd0031;
    }

    span {
      color: #dd0031;
    }
  }

  &.color-react,
  &.color-reactnative {

    &,
    &:after {
      background-color: #61dafb;
    }

    span {
      color: #61dafb;
    }
  }

  &.color-php {

    &,
    &:after {
      background-color: #8892BF;
    }

    span {
      color: #8892BF;
    }
  }

  &.color-python {

    &,
    &:after {
      background-color: #2b5b84;
    }

    span {
      color: #2b5b84;
    }
  }

  &.color-ionic {

    &,
    &:after {
      background-color: #4a8bfc;
    }

    span {
      color: #4a8bfc;
    }
  }

  &.color-phonegap {

    &,
    &:after {
      background-color: #27a1c5;
    }

    span {
      color: #27a1c5;
    }
  }

  &.color-nosql {

    &,
    &:after {
      background-color: #13aa52;
    }

    span {
      color: #13aa52;
    }
  }

  &.color-aws {

    &,
    &:after {
      background-color: #e15500;
    }

    span {
      color: #e15500;
    }
  }

  &.color-docker {

    &,
    &:after {
      background-color: #2491e5;
    }

    span {
      color: #2491e5;
    }
  }

  &.color-kubernetes {

    &,
    &:after {
      background-color: #326ce5;
    }

    span {
      color: #326ce5;
    }
  }

  &.color-html {

    &,
    &:after {
      background-color: #dc4a25;
    }

    span {
      color: #dc4a25;
    }
  }

  &.color-css {

    &,
    &:after {
      background-color: #264bdd;
    }

    span {
      color: #264bdd;
    }
  }
}

.flex-control-nav.flex-control-paging,
.flex-direction-nav {
  display: none;
}

.project {
  background-size: contain;
  box-shadow: 0px 0px 3px #dadada;

  .desc {
    opacity: 0;
    background: rgba(38, 32, 20, 0.95);
  }
}

.hire {
  color: #fff;
  background: linear-gradient(56deg, rgba(1, 0, 4, 1) 42%, rgba(137, 138, 136, 1) 77%, rgba(1, 1, 1, 1) 96%);

  .btn-hire {
    color: #fff;
    border-color: #fff;
  }
}

#ipsingh-aside {
  background: linear-gradient(90deg, #ffffff 10.51%, #f1f1f1 70.95%, #615d5d 169.82%);
  height: 100vh;
  min-height: 100vh;
  overflow-y: auto;
}

#ipsingh-home {
  height: 600px;
  max-height: 100%;

  .flexslider {
    height: 100%;
    border: none;

    .slides {
      height: 100%;

      li {
        height: 100%;
        background-position: right center;
      }
    }

    .slider-text {
      float: right;
      background: rgba(0, 0, 0, 0.4);
      height: 600px;
      max-height: 100%;

      &>.slider-text-inner {
        height: 100%;

        h1,
        h2 {
          color: #fff;
          font-weight: bold;
        }

        .btn.btn-learn {
          color: #fff;
          border-color: #fff !important;

          &:hover {
            color: #000;
            background: #fff !important;
            border: 1px solid #fff !important;
          }
        }
      }
    }
  }
}

.ipsingh-counters {
  .overlay {
    background: rgba(0, 0, 0, .8);
  }

  .ipsingh-counter-label {
    font-weight: bold;
  }
}

i.icon-angular {
  background: url('./icons/icon-angular.svg') no-repeat center;
  background-size: 100%;
}

i.icon-nodejs {
  background: url('./icons/icon-nodejs.svg') no-repeat center;
  background-size: 100%;
}

i.icon-react {
  background: url('./icons/icon-react.svg') no-repeat center;
  background-size: 100%;
}

.skill-img {
  width: 16px;
  margin-right: 5px;
}

.progress-wrap {
  h3 {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }

  .progress {
    position: relative;

    .progress-bar {
      span {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        font-weight: bold;
        background: rgba(0, 0, 0, 0.6);
        padding: 2px 8px;
        border-radius: 3px;
        color: #fff !important;
      }
    }
  }
}

.services.color-5 {
  .icon {
    background: #c8e4bd;

    &:before {
      border-color: transparent transparent #c8e4bd transparent;
    }

    &:after {
      border-color: #c8e4bd transparent transparent transparent;
    }
  }
}

.services.color-4 {
  .icon {
    background: #af1f3f;

    &:before {
      border-color: transparent transparent #af1f3f transparent;
    }

    &:after {
      border-color: #af1f3f transparent transparent transparent;
    }
  }
}

.services.color-6 {
  .icon {
    background: #459cb3;

    &:before {
      border-color: transparent transparent #459cb3 transparent;
    }

    &:after {
      border-color: #459cb3 transparent transparent transparent;
    }
  }
}

.panel-heading a {
  background-color: $primary;
}

.ipsingh-feature.ipsingh-feature-sm .ipsingh-icon i {
  color: $primary;
}

.btn-primary {
  background: $primary !important;
  border-color: $primary !important;
}

@media screen and (max-width: 768px) {

  #ipsingh-home,
  #ipsingh-counter {
    width: 100vw;
    margin-left: -1em;
    margin-right: -1em;
  }
}